import React from "react";

export default function Header() {
    return (
        <>
            <section id="header">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-12 col-md-12 p-3 p-md-5 m-0">
                            <img className="w-100 zoom" src={require("../assets/logoTxt.png").default} />
                        </div>
                        <div className="col-12 col-md-3 p-0 p-md-3 m-0 text-center">
                            <a href="https://twitter.com/Potluckprotocol" className="p-1" target="_blank">
                                <img className="zoom social-top" src={require("../assets/logos/Twitter.png").default} />
                            </a>
                            <a href="https://discord.gg/potluckprotocol" className="p-1" target="_blank">
                                <img className="zoom social-top" src={require("../assets/logos/Discord.png").default} />
                            </a>
                            <a href="https://www.youtube.com/channel/UCVvLbZzn6a-NDabTQAmDaMg" className="p-1" target="_blank">
                                <img className="zoom social-top" src={require("../assets/logos/YT.png").default} />
                            </a>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}
