import React from "react";
import ReactCardFlip from "react-card-flip";
import Data from "../projects.json";

const Card = ({ project }) => {
  const [isFlipped, setIsFlipped] = React.useState(false);
  const projectImage = require(`../assets/art/${project.frontimage}`).default;
  const projectBackImage = require(`../assets/art/${project.backimage}`).default;
  const twitterImage = require(`../assets/logos/Twitter.png`).default;
  const mintImage = require(`../assets/logos/Mint.png`).default;

  return (
    <ReactCardFlip isFlipped={isFlipped} flipDirection="horizontal">
      <div
        onMouseEnter={() => setIsFlipped((prev) => !prev)}
        className="CardFront"
      >
        <img className="w-100 p-0 m-0" src={projectImage} />
      </div>
      <div
        onMouseLeave={() => setIsFlipped((prev) => !prev)}
        className="CardBack"
      >
        <div className="p-3 p-md-4 text-center">

          <img className="w-100 p-0 m-0 mb-3 mb-md-4" src={projectBackImage} />
          <h2 className="text-center font-weight-bold text-white">{project.name}</h2>

          <a href={project.twitter} target="_blank" className="text-white"><img className="p-0 m-0 social" src={twitterImage} /></a>
          <a href={project.mintwebsite} target="_blank" className="text-white"><img className="p-0 m-0 social" src={mintImage} /></a>

        </div>
      </div>
    </ReactCardFlip>
  );
};

const Projects = () => {
  return (
    <section id="projects" className="">
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-12 col-md-8 p-3 p-md-4">
            <img className="w-100" src={require("../assets/project.png").default} />
          </div>
        </div>
        <div className="row">
          {Data.map((item, index) => (
            <div key={`card-${index}`} className="col-12 col-md-4 p-2 m-0"><div className="p-0 m-0 h-100 bg-back"><Card project={item} /></div></div>
          ))}
          <div className="col-12 col-md-4 p-2 m-0"><div className="p-0 m-0 h-100 bg-back"><img className="w-100" src={require("../assets/newprojects.jpg").default} /></div></div>
        </div>
      </div>
    </section>
  );
};

export default Projects;
